import React, { memo, useEffect, useRef } from "react";
import { TestQuestionWrapper } from "./style";
import { changeSelectedAnswerAction } from "../store/actioncreators";
import { useDispatch, useSelector, shallowEqual } from "react-redux";

export default memo(function TestQuestion(props) {
  const { question, selection, answer } = props.info;

  const questionIndex = props.index; //当前问题的下标

  const isSubmit = props.isSubmit; //是否显示答案

  //用户选择的答案
  let { answers } = useSelector(
    (state) => ({
      answers: state.answers,
    }),
    shallowEqual
  );

  useEffect(() => {
    if (answers[questionIndex - 1] === undefined) {
      answers[questionIndex - 1] = [];
    }
    for (let ref of radiosRef) {
      for (let i = 0; i < answers[questionIndex - 1].length; i++) {
        if (ref.current.value == answers[questionIndex - 1][i]) {
          ref.current.checked = true;
        }
      }
    }
  });

  const dispatch = useDispatch();

  const radiosRef = [useRef(), useRef(), useRef(), useRef()];

  return (
    <TestQuestionWrapper>
      <div className="question">
        <p>{question}</p>
      </div>
      <div className="selection">
        {selection.map((item, index) => {
          return (
            <a
              href="##"
              key={item}
              onClick={() => {
                const result = [];
                for (let ref of radiosRef) {
                  if (ref.current.checked) {
                    result.push(ref.current.value);
                  }
                }
                if (result.length > 0) {
                  dispatch(changeSelectedAnswerAction(questionIndex, result));
                }
              }}
              className={
                isSubmit && answer.includes(item) ? "right-selection" : ""
              }
            >
              <input
                type={answer.length === 1 ? "radio" : "checkbox"}
                value={item}
                id={index}
                name="selection-item"
                ref={radiosRef[index]}
              />
              <label htmlFor={index}>{item}</label>
            </a>
          );
        })}
      </div>
    </TestQuestionWrapper>
  );
});
