import styled from "styled-components";
export const TestQuestionWrapper = styled.div`
  .question {
    padding-left: 20px;
    p {
      font-size: 16px;
    }
  }

  .selection {
    a {
      display: block;
      position: relative;
      width: 960px;
      border: 1px solid #d4d4d4;
      margin-left: 17px;
      margin-bottom: 20px;
      border-radius: 2px;
      /* padding: 12px 20px 13px; */
      background: #fff;

      input {
        position: absolute;
        top: 17px;
        left: 10px;
        width: 16px;
        height: 16px;
      }

      label {
        display: inline-block;
        /* position: absolute;
        top: 10px;
        left: 10px; */
        padding-left: 36px;
        width: 930px;
        height: 50px;
        line-height: 50px;
        color: #333;
        font-size: 16px;
        cursor: pointer;
      }
    }

    a:hover {
      background-color: #f3f3f3;
      label {
        color: skyblue;
      }
    }
  }
`;
