import styled from "styled-components";
export const NewWrapper = styled.div`
  float: left;
  width: 450px;
  height: 320px;
  margin-left: 20px;
  margin-bottom: 40px;
  .img {
    position: relative;
    img {
      width: 410px;
      height: 230px;
    }

    a {
      position: absolute;
      left: 0;
      top: 0;
      width: 410px;
      height: 230px;
    }
  }

  .new-info {
    position: relative;
    width: 410px;
    height: 100px;
    font-size: 18px;
    color: #fff;
    font-weight: bold;
    background-color: pink;

    .time {
      position: absolute;
      bottom: 5px;
      left: 20px;
    }

    .title {
      position: absolute;
      top: 10px;
      left: 10px;
      a {
        color: #fff;
      }
      a:hover {
        color: red;
      }
    }
  }
`;
