import styled from "styled-components";
export const FooterWrapper = styled.div`
  height: 280px;
  background-color: #c20c0c;
  opacity: 0.7;
  .top {
    margin: 0 auto;
    width: 1400px;
    height: 160px;
    border-bottom: 1px solid #ccc;

    ul {
      height: 100px;
      display: flex;
      justify-content: space-around;
      padding-top: 30px;
      li {
        cursor: pointer;
        img {
          width: 55px;
          height: 55px;
        }

        span {
          position: relative;
          right: 4px;
          margin-top: 11px;
          font-size: 16px;
          color: #fff;
          display: block;
        }
      }
    }
  }

  .bottom {
    height: 120px;
    width: 1400px;
    margin: 0 auto;
    color: #fff;
    font-size: 16px;
    padding-top: 10px;
    div {
      padding-top: 10px;
    }
  }
`;
