import styled from "styled-components";
import left from "@/assets/img/banner-control-left.png";
import right from "@/assets/img/banner-control-right.png";
export const HomeWrapper = styled.div`
  background-color: #fff;
  .banner {
    margin: 0 auto;
    width: 1400px;
    position: relative;
    img {
      width: 1400px;
      height: 600px;
    }
    .pre {
      position: absolute;
      left: 5px;
      top: 250px;
      width: 37px;
      height: 67px;
      cursor: pointer;
      background: url(${left});
    }
    .next {
      position: absolute;
      right: 5px;
      top: 250px;
      width: 37px;
      cursor: pointer;
      height: 67px;
      background: url(${right});
    }
    .next:hover,
    .pre:hover {
      background-color: black;
      opacity: 0.5;
    }
  }

  .content {
    margin: 0 auto;
    width: 1400px;
    height: 1000px;
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
  }
`;
