import React, { memo } from "react";
import { NewWrapper } from "./style";

export default memo(function New(props) {
  const { imgUrl, time, title, link } = props.info;

  return (
    <NewWrapper>
      <div className="img">
        <img src={imgUrl} alt="" />
        <a href={link}> </a>
      </div>
      <div className="new-info">
        <div className="title">
          <a href={link}>{title}</a>
        </div>
        <div className="time">{time}</div>
      </div>
    </NewWrapper>
  );
});
