import styled from "styled-components";

export const HeaderWrapper = styled.div`
  display: flex;
  position: relative;
  /* justify-content: space-between; */
  padding-right: 10px;
  padding-bottom: 2px;
  border-bottom: 2px solid #c10d0c;

  .logo {
    height: 30px;
    width: 30px;
    background-position: -225px -156px;
  }

  .left {
    display: flex;

    .title {
      font-size: 20px;
      text-decoration: none;
      color: black;
      line-height: 28px;
    }
  }

  .right {
    margin-top: 8px;
    position: absolute;
    right: 10px;

    .icon {
      display: inline-block;
      width: 12px;
      height: 12px;
      margin-left: 4px;
      margin-top: 2px;
      background-position: 0 -240px;
    }
  }
`;
