import React, { memo } from "react";

import { NewRecommendWrapper } from "./style";
import RCMHeader from "@/components/recommend-header";
import New from "@/components/new";
import { newData } from "@/common/local-data.js";

export default memo(function NewRecommend() {
  return (
    <NewRecommendWrapper>
      <RCMHeader title="新闻推荐"></RCMHeader>
      <div className="new-content">
        {newData.map((item, index) => {
          return <New info={item} key={index}></New>;
        })}
      </div>
    </NewRecommendWrapper>
  );
});
