import React, { memo, useState } from "react";
import { NavLink } from "react-router-dom";

import { Form, Input, Button, Tooltip } from "antd";
import {
  SearchOutlined,
  CloseOutlined,
  QuestionCircleOutlined,
} from "@ant-design/icons";

import { HeaderWrapper } from "./style";
import logo from "../../assets/img/logo.png";
import { useDispatch, useSelector } from "react-redux";
import {
  changeShowAction,
  changeLoginAction,
} from "../../pages/test-online/store/actioncreators";
import axios from "axios";

export default memo(function Header() {
  const dispatch = useDispatch();

  const [loginShow, setLoginShow] = useState(false);

  const [registerShow, setRegisterShow] = useState(false);

  const [loginSuccessShow, setLoginSuccessShow] = useState(false);

  const [loginFailShow, setLoginFailShow] = useState(false);

  const [loginCheckShow, setLoginCheckShow] = useState(false);

  const [registerSuccessShow, setRegisterSuccessShow] = useState(false);

  const [username, setUsername] = useState("");

  const [psw, setPsw] = useState("");

  const layout = {
    labelCol: {
      span: 4,
    },
    wrapperCol: {
      span: 16,
    },
  };
  const tailLayout = {
    wrapperCol: {
      offset: 17,
      span: 16,
    },
  };

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 6 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 14 },
    },
  };

  const tailFormItemLayout = {
    wrapperCol: {
      xs: {
        span: 24,
        offset: 24,
      },
      sm: {
        span: 16,
        offset: 18,
      },
    },
  };

  const { isLogin } = useSelector((state) => {
    return {
      isLogin: state.isLogin,
    };
  });

  //登录数据
  const onFinish = (values) => {
    //发送登录网络请求
    console.log("Success:", values);
    const username = values.username;
    const password = values.password;
    axios
      .get("/login", {
        params: {
          username,
          password,
        },
      })
      .then(function (response) {
        console.log(response.data);
      })
      .catch(function (error) {
        console.log(error);
      });
    //dispatch(changeLoginAction(true));
    setLoginShow(false);
    if (isLogin) {
      setLoginSuccessShow(true);
    } else {
      setLoginFailShow(true);
    }
  };

  //注册表单实例
  const [form] = Form.useForm();

  //注册数据
  const onFinish_rg = (values) => {
    //发送注册网络请求
    console.log("Received values of form: ", values);
    setRegisterShow(false);
    setRegisterSuccessShow(true);
  };

  return (
    <HeaderWrapper>
      <div className="content">
        <div className="logo">
          <img src={logo} alt="" />
        </div>
        <div className="nav">
          <ul>
            <li>
              <NavLink to="/home">首页</NavLink>
            </li>
            <li>
              <NavLink to="/history">学四史</NavLink>
            </li>
            <li>
              <NavLink to="/heart">守初心</NavLink>
            </li>
            <li>
              <NavLink to="/mission">担使命</NavLink>
            </li>
            <li>
              <NavLink to="/contribution">建新功</NavLink>
            </li>
            <li>
              <NavLink
                to="/test"
                onClick={(e) => {
                  e.preventDefault();
                  if (isLogin) {
                    window.location.hash = "/test";
                  } else {
                    dispatch(changeShowAction(true));
                    setLoginCheckShow(true);
                  }
                }}
              >
                在线测试
              </NavLink>
            </li>
          </ul>
        </div>
        <Input
          placeholder="文章/新闻/资讯"
          prefix={<SearchOutlined />}
          className="search"
        />

        <div className="login">
          <a
            href="##"
            onClick={(e) => {
              e.preventDefault();
              dispatch(changeShowAction(true));
              setLoginShow(true);
            }}
          >
            登录
          </a>
        </div>
      </div>

      <div className={`login-alert ${loginShow ? "show" : "hidden"}`}>
        <div className="login-title">
          <h2>登录</h2>
          <a
            href="##"
            className="close"
            onClick={(e) => {
              e.preventDefault();
              dispatch(changeShowAction(false));
              setLoginShow(false);
            }}
          >
            <CloseOutlined />
          </a>
        </div>
        <div className="login-content">
          <Form
            {...layout}
            name="basic"
            initialValues={{
              remember: true,
            }}
            onFinish={onFinish}
            className="login-form"
          >
            <Form.Item
              label="账号"
              name="username"
              rules={[
                {
                  required: true,
                  message: "请输入账号!",
                },
              ]}
            >
              <Input
                onChange={(e) => {
                  setUsername(e.target.value);
                }}
                value={username}
              />
            </Form.Item>

            <Form.Item
              label="密码"
              name="password"
              rules={[
                {
                  required: true,
                  message: "请输入密码!",
                },
              ]}
            >
              <Input.Password
                onChange={(e) => {
                  setPsw(e.target.value);
                }}
                value={psw}
              />
            </Form.Item>

            <Form.Item {...tailLayout}>
              <Button type="primary" htmlType="submit" className="login-btn">
                登录
              </Button>
            </Form.Item>
          </Form>
        </div>
        <div className="login-footer">
          <a
            href="##"
            onClick={(e) => {
              e.preventDefault();
              setLoginShow(false);
              setRegisterShow(true);
            }}
          >
            还没有账号，快去注册
          </a>
        </div>
      </div>

      <div className={`register ${registerShow ? "show" : "hidden"}`}>
        <div className="register-title">
          <h2>注册</h2>
          <a
            href="##"
            className="close"
            onClick={(e) => {
              e.preventDefault();
              dispatch(changeShowAction(false));
              setRegisterShow(false);
            }}
          >
            <CloseOutlined />
          </a>
        </div>
        <div className={`register-content`}>
          <Form
            {...formItemLayout}
            form={form}
            name="register"
            onFinish={onFinish_rg}
            scrollToFirstError
            className="register-form"
          >
            <Form.Item
              label="账号"
              name="username"
              rules={[
                {
                  required: true,
                  message: "请输入账号!",
                },
              ]}
            >
              <Input
                onChange={(e) => {
                  setUsername(e.target.value);
                }}
                value={username}
              />
            </Form.Item>

            <Form.Item
              name="password"
              label="密码"
              rules={[
                {
                  required: true,
                  message: "请输入密码",
                },
              ]}
              hasFeedback
            >
              <Input.Password />
            </Form.Item>

            <Form.Item
              name="confirm"
              label="确认密码"
              dependencies={["password"]}
              hasFeedback
              rules={[
                {
                  required: true,
                  message: "请再次输入密码",
                },
                ({ getFieldValue }) => ({
                  validator(rule, value) {
                    if (!value || getFieldValue("password") === value) {
                      return Promise.resolve();
                    }
                    return Promise.reject(
                      "The two passwords that you entered do not match!"
                    );
                  },
                }),
              ]}
            >
              <Input.Password />
            </Form.Item>

            <Form.Item
              name="nickname"
              label={
                <span>
                  昵称&nbsp;
                  <Tooltip title="我们该怎么称呼您？">
                    <QuestionCircleOutlined />
                  </Tooltip>
                </span>
              }
              rules={[
                {
                  required: true,
                  message: "请输入你的昵称",
                  whitespace: true,
                },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item {...tailFormItemLayout}>
              <Button type="primary" htmlType="submit">
                注册
              </Button>
            </Form.Item>
          </Form>
        </div>
        <div className="register-footer">
          <a
            href="##"
            onClick={(e) => {
              e.preventDefault();
              setRegisterShow(false);
              setLoginShow(true);
            }}
          >
            已有账号，快去登录
          </a>
        </div>
      </div>

      <div className={`login-success ${loginSuccessShow ? "show" : "hidden"}`}>
        <div className="success-header">
          <h2>欢迎您！</h2>
          <a
            href="##"
            className="close"
            onClick={(e) => {
              e.preventDefault();
              dispatch(changeShowAction(false));
              setLoginSuccessShow(false);
            }}
          >
            <CloseOutlined />
          </a>
        </div>
        <div className="success-content">
          <h2>登陆成功！</h2>
        </div>
        <div className="success-footer">
          <a
            href="##"
            onClick={(e) => {
              e.preventDefault();
              setLoginSuccessShow(false);
              dispatch(changeShowAction(false));
            }}
          >
            返回
          </a>
        </div>
      </div>

      <div className={`login-fail ${loginFailShow ? "show" : "hidden"}`}>
        <div className="fail-header">
          <h2>登录失败了哦</h2>
          <a
            href="##"
            className="close"
            onClick={(e) => {
              e.preventDefault();
              dispatch(changeShowAction(false));
              setLoginFailShow(false);
            }}
          >
            <CloseOutlined />
          </a>
        </div>
        <div className="fail-content">
          <h2>登陆失败！</h2>
          <h2>请检查账号和密码</h2>
        </div>
        <div className="fail-footer">
          <a
            href="##"
            onClick={(e) => {
              e.preventDefault();
              setLoginFailShow(false);
              setLoginShow(true);
            }}
          >
            返回
          </a>
        </div>
      </div>

      <div className={`login-check ${loginCheckShow ? "show" : "hidden"}`}>
        <div className="check-header">
          <h2>你好像还没有登录哦</h2>
          <a
            href="##"
            className="close"
            onClick={(e) => {
              e.preventDefault();
              dispatch(changeShowAction(false));
              setLoginCheckShow(false);
            }}
          >
            <CloseOutlined />
          </a>
        </div>
        <div className="check-content">
          <h2>请先登录！</h2>
        </div>
        <div className="check-footer">
          <a
            href="##"
            onClick={(e) => {
              e.preventDefault();
              setLoginCheckShow(false);
              setLoginShow(true);
            }}
          >
            返回登录
          </a>
        </div>
      </div>

      <div
        className={`register-success ${
          registerSuccessShow ? "show" : "hidden"
        }`}
      >
        <div className="rg-success-header">
          <h2>注册</h2>
          <a
            href="##"
            className="close"
            onClick={(e) => {
              e.preventDefault();
              dispatch(changeShowAction(false));
              setRegisterSuccessShow(false);
            }}
          >
            <CloseOutlined />
          </a>
        </div>
        <div className="rg-success-content">
          <h2>注册成功！</h2>
        </div>
        <div className="rg-success-footer">
          <a
            href="##"
            onClick={(e) => {
              e.preventDefault();
              setRegisterSuccessShow(false);
              setLoginShow(true);
            }}
          >
            返回登录
          </a>
        </div>
      </div>
    </HeaderWrapper>
  );
});
